.gpt3 {
    &__features-container-feature {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin: 1rem; }
    &__features-container-feature-title {
        flex: 1;
        max-width: 180px;
        margin-right: 2rem;
        & h1 {
            font-family: var(--font-family);
            font-weight: 800;
            font-size: 18px;
            line-height: 24px;
            letter-spacing: -0.04em;
            color: #fff; }
        & div {
            width: 38px;
            height: 3px;
            background: var(--gradient-bar);
            box-shadow: 0 4px 4px rgba(0,0,0,0.25);
            margin-bottom: 0.25rem; } }
    &__features-container-feature-text {
        flex: 2;
        max-width: 390px;
        display: flex;
        & p {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 14px;
            line-height: 24px;
            color: var(--color-text); } } }

@media screen and (max-width: 550px) {
    .gpt3 {
        &__features-container-feature-title {
            & h1 {
                font-size: 14px;
                line-height: 22px; }
            & p {
                font-size: 12px;
                line-height: 20px; } }
        &__features-container-feature {
            margin: 1rem 0; } } }
