.gpt3 {
    &__whatgpt3 {
        display: flex;
        flex-direction: column;
        padding: 2rem;
        background: var(--color-footer);
        background: -moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
        background: -webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
        background: -o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
        background: -ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
        background: radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); }
    &__whatgpt3-feature {
        display: flex;
        & .gpt3__features-container-feature {
            margin: 0; }
        & .gpt3__features-container-feature-text {
            max-width: 700px; } }

    &__whatgpt3-heading {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 4rem 0 2rem;
        & h1 {
            font-family: var(--font-family);
            font-size: 34px;
            line-height: 45px;
            font-weight: 800;
            max-width: 510px; }
        & p {
            font-family: var(--font-family);
            font-weight: 500;
            line-height: 30px;

            color: var(--color-subtext);
            cursor: pointer; } }
    &__whatgpt3-container {
        display: flex;
        flex-wrap: wrap;
        margin-top: 2rem;

        & .gpt3__features-container-feature {
            flex: 1;
            margin: 1rem;
            min-width: 210px;
            display: unset;
            flex-direction: column; }

        & .gpt3__features-container-feature-text {
            margin-top: 0.5rem; } } }

@media screen and (max-width: 850px) {
    .gpt3 {
        &__whatgpt3-heading {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 0rem; }
        & p {
            margin-top: 1rem; } } }

@media screen and (max-width: 650px) {
    .gpt3 {
        &__whatgpt3-feature {
            & .gpt3__features-container-feature {
                flex-direction: column; }
            & .gpt3__features-container-feature-text {
                margin-top: 0.5rem; } }

        &__whatgpt3-heading {
            & h1 {
                font-size: 28px;
                line-height: 40px; } } } }

@media screen and (max-width: 350px) {
    .gpt3 {
        &__whatgpt3-container {
            & .gpt3__features-container-feature {
                margin: 1rem 0;
                min-width: 100%; } } } }
