.gpt3 {
    &__features {
        width: 100%;
        display: flex;
        justify-content: space-between; }
    &__features-heading {
        flex: 1;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        text-align: left;
        margin-right: 5rem;
        & h1 {
            font-family: var(--font-family);
            font-weight: 800;
            font-size: 34px;
            line-height: 45px; }
        & p {
            font-family: var(--font-family);
            font-weight: 500;
            line-height: 30px;
            color: var(--color-subtext);
            margin-top: 2rem; } }
    &__features-container {
        flex: 1.5;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column; } }

@media screen and (max-width: 990px) {
    .gpt3 {
        &__features {
            flex-direction: column; }
        &__features-heading {
            margin: 0 0 2rem 0; } } }

@media screen and (max-width: 550px) {
    .gpt3 {
        &__features-heading {
            & h1 {
                font-size: 28px;
                line-height: 38px; } } } }
